@import url('https://fonts.googleapis.com/css2?family=Clicker+Script&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap');

.main-step {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.espaco {
    width: 100%;
    height: 20px;
}

.title-step{
    font-family: "Clicker Script", cursive;
    font-size: 50px;
    font-weight: 400;
    font-style: normal;
    color: #182062;
}

.text-step, .text-step-ex {
    font-family: "Architects Daughter", cursive;
    font-size: 20px;
    font-weight: 400;
    font-style: normal;
    color: #182062;
    text-align: center;
}

.text-step-ex{
    width: 80%;
    text-align: left;
}

#step1-img, #step2-img, #step3-img {
    width: 450px;
    border-color: #182062;
    border-style: solid;
    border-radius: 10px;
    border-width: 2px;
}

.icon-step {
    width: 40px;
    height: 40px;
    color: #182062;
}

@media(max-width:440px){
    .title-step {
        font-size: 40px;
        text-align: center;
    }

    .text-step, .text-step-ex {
        width: 90%;
        font-size: 16px;
        text-align: center;
    }
   
    .text-step-ex{
        text-align: left;
    }
}